export const getImageUrl = (code) =>
{
    const imageMap = {
        p_1 : require('./p1.jpg'),
        p_2: require('./p2.jpg'),
        p_3 : require('./p3.jpg'),
        p_4: require('./p4.jpg'),
        p_5 : require('./p5.jpg'),
        p_6: require('./p6.jpg'),
        p_7 : require('./p7.jpg'),
        p_8: require('./p8.jpg'),
        p_9 : require('./p9.jpg'),
        p_10: require('./p10.jpg'),
        p_11 : require('./p11.jpg'),
        p_12: require('./p12.jpg'),
    };
    return imageMap[code];
}
export const justShare = (item) =>
{
    let desc = 'Item Code - '+item.code+'\n'+'Item name - '+item.title+'\n'+'Item Price -'+item.price;
    let data = {files:[],title:"Try to Learn Shop",text:desc,url:"https://vishwaganesan.github.io/Try-to-learn-shop"};
    fetch(getImageUrl(item.code)).then(res=>res.blob()).then(
        blob => {
           data.files.push(new File([blob],'hello.png',blob));
           console.log(data)
           if(!navigator.canShare)
           {
               return false;
           }
           if(navigator.canShare(data)) {
               navigator.share(data)
               return true;
           }
        }
    )
}   
