import React,{Component} from 'react';
import {withStyles} from '@material-ui/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import {getInventoryDetails} from './InventoryUtil';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import Image from './assets/images/Images';
import withWidth, { isWidthUp } from '@material-ui/core/withWidth';

const styles = {
    root: {
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        backgroundColor:'white',
        width:"100%"
      },
      gridList: {
        width: '100%',
        height: 'auto',
        overflowY:'auto'
      },
      icon: {
        color: 'rgba(255, 255, 255, 0.54)',
      },
};

const items = getInventoryDetails();
class Testjs extends Component
{
    constructor(props) 
    {
        super(props);
       
    }
    getGridListCols = () => {
        if (isWidthUp('xl', this.props.width)) {
          return 4;
        }
    
        if (isWidthUp('lg', this.props.width)) {
          return 3;
        }
    
        if (isWidthUp('md', this.props.width)) {
          return 2;
        }
    
        return 1;
      }
   render() {
       return(
    <div className={this.props.classes.root}>
      <GridList  cols={this.getGridListCols}  className={this.props.classes.gridList}>
        {items.map(tile => (
          <GridListTile  key={tile.img}>
              <Image code={tile.code} />
            <GridListTileBar
              title={tile.title}
              actionIcon={
                <IconButton aria-label={`info about ${tile.title}`} className={this.props.classes.icon}>
                  <InfoIcon />
                </IconButton>
              }
            />
          </GridListTile>
        ))}
      </GridList>
    </div>);
    
  }
}

export default withStyles(styles)(Testjs);
