import React,{Component} from 'react';
import { withStyles } from '@material-ui/styles';
import {getImageUrl} from './ImageUtil';
const styles =
{
    container:
    {   
        height:'35vmin',
        width:'35vmin'
    }
}
class Images extends Component
{
    constructor(props)
    {
        super(props);
    }
    
    render()
    {
        const url = getImageUrl(this.props.code);
        if(this.props.isCropped)
        {
        return <img  src={url} />
        }
        else
        {
            return <img  className={this.props.classes.container} src={url} /> 
        }
    };
}
export default withStyles(styles)(Images);